import { array, object, string } from 'yup';

export const formSchema = object({
	username: string()
		.required('   ')
		.min(2, '  - 2 '),
	color: string().required('   '),
	season: string().required('   '),
	checkboxGroup: array()
		.of(string().required('   '))
		.min(1, '   ')
		.required('   '),
	experience: string()
		.required('   ')
		.min(2, '  - 2 '),
});
